<template>
  <div class="p-index-wrap">
    <div class="index-main">
      <!-- <div class="swipe-wrap" v-if="bannerList.length">
        <Swipe :auto="3500">
          <mt-swipe-item v-for="(item,index) in bannerList" :key="index">
            <img :src="item.picUrl" alt="">
          </mt-swipe-item>
        </Swipe>
      </div> -->
      <div class="recommend-list-wrap">
        <p class="recommend-title">推荐歌曲</p>
        <div class="list-wrap">
          <div class="list-item" v-for="(item,index) in recommendSongList" :key="index">
            <div class="cover-img">
              <img :src="item.picUrl" alt="" class="img">
            </div>
            <p class="song-name">{{item.name}}</p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
// import { Swipe, SwipeItem } from 'mint-ui';
export default {
  data() {
    return {
      bannerList: [],
      recommendSongList: []
    };
  },
  created() {
    this.init();
  },
  methods: {
    async init() {
      await this.fetchBannerData();
      await this.fetchRecommendList();
    },
    async fetchBannerData() {
      const res = await this.$httpGet('/api/banner');
      if (res.code === 200) {
        this.bannerList = res.banners;
      }
    },
    async fetchRecommendList() {
      const res = await this.$httpGet('/api/personalized');
      if (res.code === 200) {
        this.recommendSongList = res.result;
      }
    }
  }
};
</script>
<style lang="scss">
.p-index-wrap {
  display: flex;
  flex-direction: column;
  height: 100%;
  .index-header {
    background-color: #fff;
  }
  .index-main {
    flex: 1;
    overflow-y: scroll;
    .recommend-list-wrap {
      .recommend-title {
        height: 55px;
        line-height: 55px;
        font-weight: 700;
        font-size: 16px;
        padding-left: 6px;
        position: relative;
        &::after {
          content: '';
          $width: 11px;
          display: inline-block;
          margin-left: 2px;
          width: $width;
          height: $width;
          border: 1px solid #666;
          border-left: none;
          border-bottom: none;
          transform: rotate(45deg);
        }
      }
      .list-wrap {
        display: flex;
        flex-flow: wrap;
        padding: 0 4px 4px;
        .list-item {
          flex: 0 0 33.333%;
          padding: 0 2px 7px;
          .cover-img {
            width: 100%;
            height: 0;
            border-radius: 4px;
            padding-top: 100%;
            position: relative;
            .img {
              position: absolute;
              top: 0;
              left: 0;
              width: 100%;
              height: 100%;
              object-fit: cover;
              background-color: #999;
            }
          }
          .song-name {
            word-wrap: break-word;
            word-break: break-all;
            display: -webkit-box;
            -webkit-line-clamp: 2;
            overflow: hidden;
            text-overflow: ellipsis;
            -webkit-box-orient: vertical;
          }
        }
      }
    }
  }
}
</style>



